// components/SecretIntelligence.tsx
'use client';
import { useState } from 'react';

export default function SecretIntelligence() {
  const [intelligenceReport, setIntelligenceReport] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const gatherIntelligence = async (targetCountry: string) => {
    setIsLoading(true);
    const response = await fetch(`http://localhost:8000/intelligence/${targetCountry}`);
    const data = await response.json();
    setIntelligenceReport(data.intelligence_report);
    setIsLoading(false);
  };

  return (
    <div className="secret-intelligence">
      <h2>üîí Classified Intelligence Portal</h2>
      <p>Access restricted to President only</p>
      
      <div className="intelligence-actions">
        <button onClick={() => gatherIntelligence('NEIGHBOR_COUNTRY')}>
          Gather Intel on Neighbor
        </button>
        <button onClick={() => gatherIntelligence('RIVAL_COUNTRY')}>
          Monitor Rival Activities
        </button>
      </div>

      {isLoading && <div>Generating intelligence report...</div>}
      
      {intelligenceReport && (
        <div className="intelligence-report">
          <h3>üìã Intelligence Report</h3>
          <p>{intelligenceReport}</p>
        </div>
      )}
    </div>
  );
}